.app-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
  height: 100vh;
  text-align: center;
  background-color: #012265;
  overflow-y: auto;
  padding: 5px;
}

.input-container {
  margin: 3px;  /* Reduced margin */
  padding: 3px;  /* Reduced padding */
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #d4af37;
  width: 32%;  /* Adjusted width to fit 3 per row */
  text-align: center;
  box-sizing: border-box;
}

.input-box {
  padding: 3px;  /* Reduced padding */
  margin-bottom: 3px;  /* Reduced margin */
  font-size: 12px;  /* Reduced font size */
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 90%;
  text-align: center;
  background-color: white;
  color: white;
}

.input-box::placeholder {
  color: white;
  opacity: 0.7;
}

.top-input {
  background-color: #0A3D91;
}

.bottom-input {
  background-color: #B22222;
}

.team-data-container {
  text-align: center;
  background-color: #d4af37;
  border-radius: 5px;
  padding: 3px;  /* Reduced padding */
  margin-top: 0;  /* Removed margin */
}

.section-header {
  font-weight: bold;
  margin-top: 3px;  /* Reduced margin */
  margin-bottom: 3px;  /* Reduced margin */
  font-size: 12px;  /* Reduced font size */
}

.grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1px;  /* Reduced gap */
}

.grid-item {
  border: 1px solid #ddd;
  padding: 1px;  /* Reduced padding */
  background-color: #f2f2f2;
  font-size: 12px;  /* Reduced font size */
}

.additional-content {
  margin-top: 20px;  /* Add some space above */
  padding: 20px;
  background-color: #d4af37;
  border-radius: 5px;
  width: 100%;
  text-align: center;
  color: #012265;
    /* Enable vertical scrolling if necessary */
  max-height: 80vh;  /* Set a maximum height relative to the viewport height */
}

.map-container {
  position: relative;
  width: 100%;
  height: auto;
}

.map-image {
  width: 100%;
  height: auto;
  object-fit: contain;
}

.marker {
  position: absolute;
  width: 10px;
  height: 10px;
  background-color: red;
  border-radius: 50%;
  transform: translate(-50%, -50%);
}
